<template>
    <div class="locale-changer">
        <select class="form-control form-control-sm" @change="onChangeLocale" v-model="lang">
            <option v-for="(lang, i) in langs" :key="`Lang${i}`" :value="lang.value">{{ lang.text }}</option>
        </select>
    </div>
</template>

<script>
    export default {
        name: 'locale-changer',
        data() {
            return {
                lang: this.$i18n.locale,
                langs: [{
                    text: '中文',
                    value: 'tw'
                }, {
                    text: 'English',
                    value: 'en'
                }]
            }
        },
        methods: {
            onChangeLocale() {
                location.href = (this.lang === 'tw' ? '' : '/' + this.lang) + this.$route.fullPath;
            }
        }
    }
</script>