import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

Vue.use(Router);
Vue.use(Meta);

let locale = window.location.pathname.replace(/^\/([^\/]+).*/i,'$1');

const router = new Router({
    mode: 'history',
    base: (locale.trim().length === 2 && locale != "/") ? process.env.BASE_URL + locale : process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            component: () => import('./views/Home.vue')
        },
        {
            path: '/about*',
            name: 'about',
            component: () => import('./views/About.vue')
        },
        {
            path: '/disclaimer*',
            name: 'disclaimer',
            component: () => import('./views/Disclaimer.vue')
        },
        {
            path: '/tools/codecs*',
            name: 'codecs',
            component: () => import('./views/tool/Codecs.vue')
        },
        {
            path: '/tools/hash*',
            name: 'hashGenerator',
            component: () => import('./views/tool/HashGenerator.vue')
        },
        {
            path: '/tools/luckyDraw*',
            name: 'luckyDraw',
            component: () => import('./views/tool/LuckyDraw.vue')
        },
        {
            path: '/tools/passwordGenerator*',
            name: 'passwordGenerator',
            component: () => import('./views/tool/PasswordGenerator.vue')
        },
        {
            path: '/tools/timer*',
            name: 'timer',
            component: () => import('./views/tool/Timer.vue')
        },
        {
            path: '/tools/qrCode*',
            name: 'qrCode',
            component: () => import('./views/tool/QrCode.vue')
        },
        {
            path: '*',
            component: () => import('./views/NotFound.vue'),
        }
    ]
});

export default router;